

.home-container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
}
.home-container1 {
  width: 100%;
  height: 1040px;
  display: flex;
  position: relative;
  text-align: center;
  align-items: center;
  margin-bottom: var(--dl-space-space-twounits);
  flex-direction: column;
  background-size: cover;
  justify-content: center;
  background: linear-gradient(90deg, rgba(79,250,232,1) 0%, rgba(99,115,250,1) 80%, rgba(70,89,255,1) 100%);
  background-position: center;
}
.home-heading {
  color: rgb(255, 255, 255);
  width: 779px;
  height: 168px;
  font-size: 94px;
  font-style: normal;
  font-weight: 900;
  border-color: #ffffff;
  border-width: 12px;
  margin-bottom: var(--dl-space-space-fourunits);
  text-transform: uppercase;
}
.LoginButton {
  color: rgb(122, 177, 255);
  padding: 10 rem;
  font-size: 40px;
  font-style: normal;
  font-family: Open Sans;
  font-weight: 700;
  margin-bottom: var(--dl-space-space-twounits);
  padding-right: 10 rem;
  text-transform: uppercase;
}

.home-image {
  left: -14px;
  width: 915px;
  bottom: -2px;
  height: 563px;
  position: absolute;
  object-fit: cover;
  transition: 0.3s;
}
.home-image:hover {
  opacity: 100;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, rgba(79,250,232,1) 0%, rgba(99,115,250,1) 80%, rgba(70,89,255,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

